import React from 'react';
import button from './images/btn_google_signin_dark_normal_web.png';

const loginButton = (props) => {

  return (
    <a href='http://localhost:3001/auth/googleAuth'>
      <img
        src={button}
        alt="signin with google"
        // onClick={props.click}
      />
    </a>
  )
}

export default loginButton;



